.wprx-cil__popover {
  border: 1px solid #ccc;
  background-color: #fff;
  box-shadow: 2px 2px 4px 2px rgba(154, 154, 154, 0.5);
}

.wprx-cil__popover-content {
  min-width: 300px;
  padding: 15px;
  border-radius: 5px;
  border: none;
}

.components-base-control__field {
  margin-bottom: 8px;
}

.priceArrowTo {
  width: 16px;
  text-align: center;
  padding-top: 28px;
}

.priceTo {
  text-align: center;
  background-color: #9f9f9f;
  margin-top: 20px;
}

.components-text-control__input:disabled {
  cursor: e-resize;
  background-color: #e0e0e0;
  border-color: #b9b9b9;
  color: #4a4a4a;
  font-weight: 600;
}

@media screen and (min-width: 800px) {
  .wprx-cil__popover-input-inline.wprx-cil__popover-input > div,
  .wprx-cil__popover-input-inline.wprx-cil__popover-select {
    display: flex;
    align-items: baseline;
    gap: 8px;
    position: relative;
  }
  .wprx-cil__popover-input-inline.wprx-cil__popover-input > div > .components-base-control__label,
  .wprx-cil__popover-input-inline.wprx-cil__popover-select > .components-flex-item {
    width: 120px !important;
    font-size: 12px;
    color: #262626;
    margin-bottom: 0 !important;
    height: auto;
  }
  .wprx-cil__popover-input-inline.wprx-cil__popover-input > div > .components-text-control__input,
  .wprx-cil__popover-input-inline.wprx-cil__popover-input > .components-input-control__container {
    max-width: 100% !important;
    width: auto !important;
    border-color: #a6a6a6;
    padding: 0px 8px !important;
    flex-grow: 1;
  }
  /*price[data-rich-text-format-boundary="true"]*/
}
.block-editor-block-list__block .components-text-control__input {
  padding-top: 0 !important;
  padding-bottom: 0 !important;
}

